<script>

    export default {
        name: 'FirmDialog',
        components:{
            
        },
        data(){
            return{
                valid_firmForm: false,
                text_requiredRules: [
                    v => !!v || '',
                ],
                text_requiredRules_isNumber: [
                    v => !!v || '',
                    v => {
                        return !!parseFloat(v) || ''
                    },
                ],
            };
        },
        props:{
            prop_firmDialog:{
                type:Boolean,
                required:true
            },
            prop_text_cardTitle:{
                type:String,
                required:true
            },
            prop_text_confirmBtn:{
                type:String,
                required:true
            },
            prop_firmID:{
                type:Number,
                required:false
            },
            prop_firmName:{
                type:String,
                required:false
            },
            prop_firmAddress:{
                type:String,
                required:false
            },
            prop_remark:{
                type:String,
                required:false
            },
        },
        computed:{
            firmDialog:{
                get(){
                    return this.prop_firmDialog
                },
                set(val){
                    this.$emit('update:prop_firmDialog',val)
                    
                }
            },
            text_cardTitle:{
                get(){
                    return this.prop_text_cardTitle
                },
                set(val){
                    this.$emit('update:prop_text_cardTitle',val)
                }
            },
            text_confirmBtn:{
                get(){
                    return this.prop_text_confirmBtn
                },
                set(val){
                    this.$emit('update:prop_text_confirmBtn',val)
                }
            },
            firmID:{
                get(){
                    return this.prop_firmID
                },
                set(val){
                    this.$emit('update:prop_firmID',val)
                }
            },
            firmName:{
                get(){
                    return this.prop_firmName
                },
                set(val){
                    this.$emit('update:prop_firmName',val)
                }
            },
            firmAddress:{
                get(){
                    return this.prop_firmAddress
                },
                set(val){
                    this.$emit('update:prop_firmAddress',val)
                }
            },
            remark:{
                get(){
                    return this.prop_remark
                },
                set(val){
                    this.$emit('update:prop_remark',val)
                }
            },
        },
        methods:{
            onClick_confirm:function(){ // childevent
                this.$refs.form.validate();
                if(this.valid_firmForm == false){
                    return
                }
                var firmInfo = {};
                firmInfo.firmID = this.firmID;
                firmInfo.firmName = this.firmName;
                firmInfo.firmAddress = this.firmAddress;
                firmInfo.remark = this.remark;
                this.$emit('confirmClick',firmInfo);// childEvnet
                this.resetForm();
            },
            onClick_cancel(){
                this.firmDialog = false;
                this.resetForm();
            },
            resetForm () {
                this.firmID = null;
                this.firmName = null;
                this.firmAddress = null;
                this.remark = null;
                this.$refs.form.reset();
            },
        },
        watch:  {
            firmDialog: function(){
                if(this.firmDialog == false){
                    this.resetForm();
                }
            },
        }
    }
</script>
<template src="./template.html"></template>